body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.jumbotron p{
  font-size: 18px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

nav.navbar {
  width: 100%;
	background-color: black !important;
}

.navbar-light .navbar-nav .nav-link {
	color: #404040;
	background-color: #C0C0C0;
}
.navbar-nav > a{
	color: #404040;
	transition: 
    border-bottom-left-radius 0.5s, 
    border-bottom-right-radius 0.5s, 
    border-top-right-radius 0.5s,
    border-top-left-radius 0.5s;
	background-color: #C0C0C0;
	margin-top: 0px;
	margin-bottom: 0px;
	margin-right: 2px;
	margin-left: 2px;
	padding: 5px;
	font-style: normal;
	-webkit-font-feature-settings: "c2sc";
	        font-feature-settings: "c2sc";
	font-variant: small-caps;
	font-weight: 800;
	font-size: 15px;	
	border-radius: 25px;
	width: 150px;
	text-align: center;
	text-shadow: 0 1px 0 rgba(0, 0, 0, 0.32);
	outline: 0;
}

.navbar-nav>a:focus, .navbar-nav>a:hover{
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
	border-top-left-radius: 4px;
	border-top-right-radius: 4px;
	background-color: #C0C0C0;
	text-shadow: 0 1px 0 rgba(0, 0, 0, 0.32);
}

.navbar-light .navbar-toggle:focus, .navbar-light .navbar-toggle:hover{
	background-color: #D3D3D3;
}

.navbar-light .navbar-toggle:active, .navbar-light .navbar-toggle:active{
	color: #C0C0C0;
	background-color: #D3D3D3;
}

.navbar-light .navbar-brand, .navbar-light .navbar-brand:hover{
	color: #C0C0C0;
	font-family: "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif; 
	-webkit-font-feature-settings: "c2sc"; 
	        font-feature-settings: "c2sc"; 
	font-variant: small-caps;
	font-weight: 700;
	text-shadow: 0 1px 0 rgba(0, 0, 0, 0.32);
}


.navbar-light .navbar-toggler {
    background-color: #C0C0C0; 
    color: #404040;
}


/* adjustments made to the page orientation upon change of screen size */
@media screen and (max-width: 410px) {
  .navbar-brand{
    font-size: 25px;
  }
}

@media screen and (min-width: 409px) {
  .navbar-brand{
    font-size: 35px;
  }
}

@media screen and (max-width: 993px) {
  .navbar-nav > a{
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
  }
}

